import React from "react";
import "../MyTeam/MyTeamLayout.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faRightLeft, faPlus } from "@fortawesome/free-solid-svg-icons";


export default function emptyContainer(props) {

    const hov = props.hov;
    const notHov = props.notHov;

    return (
        <div
          className="swap_container"
          style={props.hovered === props.hovVal ? hov : notHov}
        >
          <FontAwesomeIcon icon={faPlus} className="plus_symbol" />
        </div>
      )
}