import React, { useEffect } from "react";
import "../MyTeam/MyTeamLayout.css";

import playerData from "../playerData";
import EmptyContainer from "./EmptyContainer";
import PictureContainer from "./PictureContainer";

export default function teamSelection(props) {
  const hovered = {
    backgroundColor: "#384859",
  };

  const notHovered = {
    backgroundColor: "#22222c",
    color: "#778cac",
  };

  const hoveredInner = {
    backgroundColor: "#6333ff",
    cursor: "pointer",
    color: "#eef5f8",
  };

  const notHoveredInner = {
    backgroundColor: "#22222c",
    cursor: "pointer",
    color: "#384859",
  };

  const hoveredI = {
    transform: "scale(1) translateY(0px) translateX(-20px)",
  };

  const notHoveredI = {
    transform: "scale(1)",
  };

  const visible = {
    opacity: "1",
  };

  const notVisible = {
    opacity: "0",
    scale: "0.05",
  };

  let roster = playerData.filter((el) => {
    return props.selectedPlayers.some((f) => {
      return f.name === el.name;
    });
  });

  useEffect(() => {
    roster = playerData.filter((el) => {
      return props.selectedPlayers.some((f) => {
        return f.name === el.name;
      });
    });
  }, [props.selectedPlayers]);

  return (
    <div className="team__selection">
      <div
        className="selection__card"
        style={props.hovered === "Top" ? hovered : notHovered}
        onMouseOver={() => props.hoverFunction("Top")}
        onMouseOut={() => props.hoverFunction(0)}
        onClick={() => props.clickFunction("Top")}
      >
        {roster[4].name === "NoTop" ? (
          <EmptyContainer
            hovered={props.hovered}
            hovVal={"Top"}
            hov={hoveredInner}
            notHov={notHoveredInner}
          />
        ) : (
          <PictureContainer
            roster={roster}
            hovered={props.hovered}
            hovVal={"Top"}
            val={4}
            hov={hoveredI}
            notHov={notHoveredI}
            vis={visible}
            notVis={notVisible}
          />
        )}
      </div>
      <div
        className="selection__card"
        style={props.hovered === "Jungle" ? hovered : notHovered}
        onMouseOver={() => props.hoverFunction("Jungle")}
        onMouseOut={() => props.hoverFunction(0)}
        onClick={() => props.clickFunction("Jungle")}
      >
        {roster[3].name === "NoJungle" ? (
          <EmptyContainer
            hovered={props.hovered}
            hovVal={"Jungle"}
            hov={hoveredInner}
            notHov={notHoveredInner}
          />
        ) : (
          <PictureContainer
            roster={roster}
            hovered={props.hovered}
            hovVal={"Jungle"}
            val={3}
            hov={hoveredI}
            notHov={notHoveredI}
            vis={visible}
            notVis={notVisible}
          />
        )}
      </div>
      <div
        className="selection__card"
        style={props.hovered === "Mid" ? hovered : notHovered}
        onMouseOver={() => props.hoverFunction("Mid")}
        onMouseOut={() => props.hoverFunction(0)}
        onClick={() => props.clickFunction("Mid")}
      >
        {roster[2].name === "NoMid" ? (
          <EmptyContainer
            hovered={props.hovered}
            hovVal={"Mid"}
            hov={hoveredInner}
            notHov={notHoveredInner}
          />
        ) : (
          <PictureContainer
            roster={roster}
            hovered={props.hovered}
            hovVal={"Mid"}
            val={2}
            hov={hoveredI}
            notHov={notHoveredI}
            vis={visible}
            notVis={notVisible}
          />
        )}
      </div>
      <div
        className="selection__card"
        style={props.hovered === "Adc" ? hovered : notHovered}
        onMouseOver={() => props.hoverFunction("Adc")}
        onMouseOut={() => props.hoverFunction(0)}
        onClick={() => props.clickFunction("Adc")}
      >
        {roster[1].name === "NoAdc" ? (
          <EmptyContainer
            hovered={props.hovered}
            hovVal={"Adc"}
            hov={hoveredInner}
            notHov={notHoveredInner}
          />
        ) : (
          <PictureContainer
            roster={roster}
            hovered={props.hovered}
            hovVal={"Adc"}
            val={1}
            hov={hoveredI}
            notHov={notHoveredI}
            vis={visible}
            notVis={notVisible}
          />
        )}
      </div>
      <div
        className="selection__card"
        style={props.hovered === "Sup" ? hovered : notHovered}
        onMouseOver={() => props.hoverFunction("Sup")}
        onMouseOut={() => props.hoverFunction(0)}
        onClick={() => props.clickFunction("Sup")}
      >
        {roster[0].name === "NoSup" ? (
          <EmptyContainer
            hovered={props.hovered}
            hovVal={"Sup"}
            hov={hoveredInner}
            notHov={notHoveredInner}
          />
        ) : (
          <PictureContainer
            roster={roster}
            hovered={props.hovered}
            hovVal={"Sup"}
            val={0}
            hov={hoveredI}
            notHov={notHoveredI}
            vis={visible}
            notVis={notVisible}
          />
        )}
      </div>
    </div>
  );
}
