import React from "react";

export default function trendingRanks(props) {
  const sorted = { color: "#778cac" };
  const notSorted = { color: "#3e4859" };

  return (
    <div className="transfer_element">
      <div className="trans_rank brighter">{props.rank}</div>
      <div className="trans_name brighter">{props.name}</div>
      <div className="inVal" style={props.count === 0 ? sorted : notSorted}>{props.in}</div>
      <div className="outVal" style={props.count === 1 ? sorted : notSorted}>{props.out}</div>
    </div>
  );
}
