import React, { useState } from "react";
import "./Dashboard.css";
import "./Selection/SelectionPanel.css";
import PlayerList from "./PlayerList/PlayerList";
import TeamList from "./TeamList/TeamList";
import MyData from "./MyData/MyData";
import MyTeam from "./MyTeam/MyTeamLayout";
import Upcoming from "./UpcomingGames/Upcoming";
import TrendingTransfers from "./TrendingTransfers/TrendingTransfers";
import League from "./League/League";
import LeagueRules from "./League/LeagueRules";
import "./playerData.js";


export default function dashboard() {
  // function ???
  const [pages, setPages] = useState(0);

  function changePages(val) {
    setPages(val);
  }

  // function ???
  const [role, setRole] = useState();

  const setRoleFunction = (val) => {
    setRole(val);
  };

  // function ???
  const [selectionTabShown, setSelectionTabShown] = useState(0);

  function openSelectionTab(val) {
    setSelectionTabShown(val);
  }

  const [selectedPlayers, setSelectedPlayers] = useState([
    { role: "Sup", name: "Vulcan" },
    { role: "Adc", name: "FBI" },
    { role: "Mid", name: "Bjergsen" },
    { role: "Jungle", name: "Spica" },
    { role: "Top", name: "Solo" },
  ]);

  const selectPlayer = (role, name) => {
    setSelectedPlayers((current) =>
      current.map((obj) => {
        if (obj.role === role) {
          return { role: role, name: name };
        }
        setSelectionTabShown(0);
        return obj;
      })
    );
  };

  const renderSwitchSelection = () => {
    if (selectionTabShown !== 0) {
      return (
        <PlayerList
          closeFunction={openSelectionTab}
          selectionFunction={selectPlayer}
          role={selectionTabShown}
        />
      );
    } else {
      return (
        <>
          <div className="selection_panel">
            <ul className="dash_nav">
              <li onClick={() => changePages(0)}>
                <span className={pages === 0 ? "active" : null}>
                  Team Overview
                </span>
              </li>
              <li onClick={() => changePages(1)}>
                <span className={pages === 1 ? "active" : null}>Leagues</span>
              </li>
            </ul>
          </div>
          <div className="main_dash_container">
            <div className="left_side">{renderSwitchDashboard(pages)}</div>
            <div className="right_side">{renderSwitchRight(pages)}</div>
          </div>
        </>
      );
    }
  };

  const renderSwitchDashboard = (val) => {
    if (val === 1) {
      return <League />;
    } else {
      return (
        <div>
          <MyTeam
            roster={selectedPlayers}
            handleClick={openSelectionTab}
            setRoleFunction={() => setRoleFunction()}
          />
          <div className="lower">
            <div className="lower_inner">
              <TrendingTransfers />
            </div>
            <div className="lower_inner">
              <Upcoming />
            </div>
          </div>
        </div>
      );
    }
  };

  const renderSwitchRight = (val) => {
    if (val === 1) {
      return <LeagueRules />;
    } else {
      return (
        <div>
          <MyData />
          <TeamList />
        </div>
      );
    }
  };

  return (
    <div className="dashboard_container" data-scroll-section>
      {renderSwitchSelection()}
    </div>
  );
}
