import React from "react";
import "./ChartOne.css";

export default function chartOne(props) {
  const active = {
    backgroundColor: "#6333ff",
    color: "#fff",
  };

  const notActive = {
    backgroundColor: "#212330",
    color: "#778cac",
  };

  return (
    <div className="points_bar__container">
      <div className="points__bar brighter">
        <div
          className="top pb_child"
          style={props.hovered === 6 ? active : notActive}
        >
          Solo
        </div>
        <div
          className="jungle pb_child"
          style={props.hovered === 7 ? active : notActive}
        >
          Spica
        </div>
        <div
          className="mid pb_child"
          style={props.hovered === 8 ? active : notActive}
        >
          Palafox
        </div>
        <div
          className="adc pb_child"
          style={props.hovered === 9 ? active : notActive}
        >
          FBI
        </div>
        <div
          className="sup pb_child"
          style={props.hovered === 10 ? active : notActive}
        >
          CoreJJ
        </div>
      </div>
      <h4>GW Pts by player</h4>
    </div>
  );
}
