import React from "react";
import "./MyData.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowRight } from "@fortawesome/free-solid-svg-icons";

export default function mydata() {
  return (
    <div className="points_and_ranks">
      <div className="points_and_ranks_inner">
      <h3 className="brighter">Points and Rankings</h3>
      <div className="pr">
        <p>GW Points</p>
        <h4>99</h4>
      </div>
      <div className="pr">
        <p>GW Rank</p>
        <h4>23</h4>
      </div>
      <div className="pr">
        <p>Overall Points</p>
        <h4>1600</h4>
      </div>
      <div className="pr">
        <p>Overall Rank</p>
        <h4>11</h4>
      </div>
      <div className="pr">
        <p>Total Players</p>
        <h4>200</h4>
      </div>
      <div className="gameweek_link">
        <p>View Gameweek history</p>
        <FontAwesomeIcon icon={faArrowRight} />
      </div>
    </div>
    </div>
  );
}
