import React from "react";
import "../MyTeam/MyTeamLayout.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faRightLeft, faPlus } from "@fortawesome/free-solid-svg-icons";

export default function pictureContainer(props) {

    const val = props.val;
    const hovVal = props.hovVal;

    const hov = props.hov;
    const notHov = props.notHov;

    const vis = props.vis;
    const notVis = props.notVis;

    const roster = props.roster;

    return (
        <div className="picture__container">
          <div className="logo__container">
            <img
              src={require(`../PlayerList/Player/orgLogo/${props.roster[val].team}.png`)}
              alt="tsm logo"
              className="logo"
              style={props.hovered === hovVal ? hov : notHov}
            />
          </div>
          <div className="image">
            <img
              src={require(`../PlayerList/Player/${hovVal}/${roster[val].img}.png`)}
              alt="player"
              className="player__picture"
              style={props.hovered === hovVal ? hov : notHov}
            />
            <FontAwesomeIcon
              icon={faRightLeft}
              className="arrow_symbol"
              style={props.hovered === hovVal ? vis : notVis}
            />
          </div>
        </div>
      )
}