import React from "react";
import './League.css';

export default function leagueRules() {
    return (
        <div className="league_rules">
            <div className="rules_title"><h4>Rules</h4></div>
            <ul className="rules_list">
                <li>Roster max value is 1000</li>
                <li>Only 2 players max from one team</li>
                <li>Substitiutions must be done before lock-in</li>
                <li>Lock-in is 1 hour before the first game of the week</li>
                <li>Only 2 substitiutions per week</li>
                <li>User gets an extra substitiution for real life roster changes</li>
            </ul>
        </div>
    )
}