import React from "react";
import "../MyTeam/MyTeamLayout.css";
import "./MainInfoContainer.css";
import { ReactComponent as Cross } from "../images/Cross.svg";

import ChartOne from "../ChartOne/ChartOne";
import ChartTwo from "../ChartTwo/ChartTwo";
import ChartThree from "../ChartThree/ChartThree";
import ChartFour from "../ChartFour/ChartFour";

import PointsTableOne from "../PointsTableOne/PointsTableOne";

import playerSummerData from "../LCS Summer Split 2022 Games.json";

// -------------------------- //

export default function mainInfoContainer(props) {
  // console.log(props.stats)
  switch (props.test) {
    case 0:
      return (
        <div className="main_info__container">
          <button
            className="close_player_info"
            onClick={() => props.clickFunction(5)}
          >
            <Cross className="close_symbol" />
          </button>
          <PointsTableOne stats={props.stats} />
          <PointsTableOne stats={props.stats} />
        </div>
      );
    case 1:
      return (
        <div className="main_info__container">
          <button
            className="close_player_info"
            onClick={() => props.clickFunction(5)}
          >
            <Cross className="close_symbol" />
          </button>
          <PointsTableOne stats={props.stats} />
          <PointsTableOne stats={props.stats} />
        </div>
      );
    case 2:
      return (
        <div className="main_info__container">
          <button
            className="close_player_info"
            onClick={() => props.clickFunction(5)}
          >
            <Cross className="close_symbol" />
          </button>
          <PointsTableOne stats={props.stats} />
          <PointsTableOne stats={props.stats} />
        </div>
      );
    case 3:
      return (
        <div className="main_info__container">
          <button
            className="close_player_info"
            onClick={() => props.clickFunction(5)}
          >
            <Cross className="close_symbol" />
          </button>
          <PointsTableOne stats={props.stats} />
          <PointsTableOne stats={props.stats} />
        </div>
      );
    case 4:
      return (
        <div className="main_info__container">
          <button
            className="close_player_info"
            onClick={() => props.clickFunction(5)}
          >
            <Cross className="close_symbol" />
          </button>
          <PointsTableOne stats={props.stats} />
          <PointsTableOne stats={props.stats} />
        </div>
      );
    case 5:
      return (
        <div className="main_info__container">
          <div className="points_comparison_container">
            <ChartThree />
            <ChartFour />
          </div>
          <div className="points_bar_container">
            <ChartTwo />
            <ChartOne hovered={props.hovered} />
          </div>
        </div>
      );
    default:
      return (
        <div className="main_info__container">
          <div className="points_comparison_container">
            <ChartThree />
            <ChartFour />
          </div>
          <div className="points_bar_container">
            <ChartTwo />
            <ChartOne hovered={props.hovered} />
          </div>
        </div>
      );
  }
}
