import React from "react";
import "./PointsTable.css";
import { ReactComponent as Kills } from "../images/SoloKill.svg";
import { ReactComponent as Deaths } from "../images/Deaths.svg";
import { ReactComponent as Assist } from "../images/Assist.svg";
import { ReactComponent as CS } from "../images/CS.svg";
import { ReactComponent as SoloKill } from "../images/SoloKill.svg";
import { ReactComponent as FirstBlood } from "../images/FirstBlood.svg";
import { ReactComponent as FirstTurret } from "../images/FirstTower.svg";
import { ReactComponent as Dragon } from "../images/Dragon.svg";
import { ReactComponent as FirstBaron } from "../images/FirstBaron.svg";
import { ReactComponent as Tick } from "../images/Tick.svg";
import { ReactComponent as Cross } from "../images/Cross.svg";

export default function pointsTableOne(props) {
  // console.log(props.stats);
  return (
    <div className="points_table_one">
      <div className="points_title_bar">
        <div className="player_pts_title">PLAYER PTS</div>
        <div className="team_pts_title">TEAM PTS</div>
      </div>
      <div className="points_table_container">
        <table className="points_table brighter">
          <tbody>
            <tr>
              <td>
                <Kills />
              </td>
              <td>
                <Deaths />
              </td>
              <td>
                <Assist />
              </td>
              <td>
                <CS />
              </td>
              <td style={{ color: "#995e754f" }}>
                <SoloKill />
              </td>
              <td>
                <FirstBlood />
              </td>
              <td style={{ color: "#995e754f" }}>
                <CS />
              </td>
              <td style={{ color: "#995e754f" }}>
                <FirstTurret />
              </td>
              <td style={{ color: "#995e754f" }}>
                <Dragon />
              </td>
              <td style={{ color: "#db43db5b" }}>
                <FirstBaron />
              </td>
              <td style={{ color: "#c48e2b73" }}>
                <Dragon />
              </td>
              <td style={{ color: "#1c6ea473" }}>
                <Dragon />
              </td>
            </tr>
            <tr>
              <td>{props.stats.kills}</td>
              <td>{props.stats.deaths}</td>
              <td>{props.stats.assists}</td>
              <td>{props.stats.cs}</td>
              <td>{props.stats.gpm}</td>
              <td>{props.stats.fb === 0 ? <Cross /> : <Tick />}</td>
              <td>{props.stats.cs15}</td>
              <td>{props.stats.ft === 0 ? <Cross /> : <Tick />}</td>
              <td>{props.stats.fd === 0 ? <Cross /> : <Tick />}</td>
              <td>{props.stats.fBaron === 0 ? <Cross /> : <Tick />}</td>
              <td>{props.stats.drags === 0 ? <Cross /> : props.stats.drags}</td>
              <td>
                {props.stats.elders === 0 ? <Cross /> : props.stats.elders}
              </td>
            </tr>
            <tr>
              <td>{props.stats.kills === 0 ? "-" : props.stats.killScore}</td>
              <td>{props.stats.deaths === 0 ? "-" : props.stats.deathScore}</td>
              <td>{props.stats.assists === 0 ? "-" : props.stats.assistsScore}</td>
              <td>{props.stats.csScore}</td>
              <td>{props.stats.gpmScore}</td>
              <td>{props.stats.fbScore}</td>
              <td>{props.stats.cs15Score}</td>
              <td>{props.stats.ft === 0 ? "-" : props.stats.ftScore}</td>
              <td>{props.stats.fd === 0 ? "-" : props.stats.fdScore}</td>
              <td>{props.stats.fBaron === 0 ? "-" : props.stats.fBaronScore}</td>
              <td>{props.stats.drags === 0 ? "-" : props.stats.dragScore}</td>
              <td>{props.stats.elders === 0 ? "-" : props.stats.elderScore}</td>
            </tr>
          </tbody>
        </table>
        <div className="total_container brighter">
          <p>Total</p>
          <p className="highest">{props.stats.tot}</p>
        </div>
      </div>
    </div>
  );
}
