import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronUp, faChevronDown, faChevronRight } from "@fortawesome/free-solid-svg-icons";

export default function userEl(props) {
  return (
    <div className="user_position">
      <div className="user_p_children rank_div">
        <FontAwesomeIcon icon={props.chevron ? faChevronUp : faChevronDown} className={props.chevron ? "chevron_up" : "chevron_down"} />
      </div>
      <div className="user_p_children"><p>{props.rank}</p></div>
      <div className="user_p_children brighter"><p>{props.teamName}</p></div>
      <div className="more_info">
        <FontAwesomeIcon icon={faChevronRight} className="chevron_right" />
      </div>
    </div>
  );
}
