import React, { useState } from "react";
import "./TrendingTransfers.css";
import TrendingRanks from "./TrendingRanks";

export default function trendingTransfers() {
  const [count, setCount] = useState(0);

  const [transferData, setTransferData] = useState([
    {
      rank: "1",
      name: "CoreJJ",
      in: 28,
      out: 2,
    },
    {
      rank: "2",
      name: "Fudge",
      in: 25,
      out: 6,
    },
    {
      rank: "3",
      name: "Bjergsen",
      in: 20,
      out: 0,
    },
    {
      rank: "4",
      name: "eXyu",
      in: 19,
      out: 1,
    },
    {
      rank: "5",
      name: "Aphromoo",
      in: 13,
      out: 5,
    },
    {
      rank: "6",
      name: "Hans Sama",
      in: 9,
      out: 3,
    },
    {
      rank: "7",
      name: "Toucouille",
      in: 8,
      out: 8,
    },
    {
      rank: "8",
      name: "Biofrost",
      in: 7,
      out: 12,
    },
    {
      rank: "9",
      name: "IgNar",
      in: 6,
      out: 19,
    },
  ]);

  function sortIn() {
    setTransferData((prevState) => {
      return prevState.sort((a, b) => b.in - a.in);
    });
    setCount(0);
  }

  const sortOut = () => {
    setTransferData((prevState) => {
      return prevState.sort((a, b) => b.out - a.out);
    });
    setCount(1);
  };

  const highlighted = { color: "#eef5f8" };

  let transferElements = transferData.map((el) => {
    return (
      <TrendingRanks
        rank={transferData.indexOf(el) + 1}
        name={el.name}
        in={el.in}
        out={el.out}
        key={el.name}
        count={count}
      />
    );
  });

  return (
    <div className="trending_transfers">
      <h3 className="brighter">Trending Transfers</h3>
      <button
        className="in trans_btn"
        onClick={() => {
          sortIn();
        }}
      >
        <p style={count === 0 ? highlighted : null}>In</p>
      </button>
      <button
        className="out trans_btn"
        onClick={() => {
          sortOut();
        }}
      >
        <p style={count === 1 ? highlighted : null}>Out</p>
      </button>
      {transferElements}
    </div>
  );
}
