import React from "react";
import "./TeamList.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronUp, faChevronRight } from "@fortawesome/free-solid-svg-icons";
import LeaderboardData from "./LeaderboardData";
import UserEl from "./UserEl";

const user = (
  <div className="user_position">
    <div className="user_p_children rank_div">
      <FontAwesomeIcon icon={faChevronUp} className="chevron_up" />
    </div>
    <div className="user_p_children">1</div>
    <div className="user_p_children brighter">McDonalds</div>
    <div className="more_info">
      <FontAwesomeIcon icon={faChevronRight} className="chevron_right" />
    </div>
  </div>
);

const userElements = LeaderboardData.map((el) => {
  return <UserEl chevron={el.chevron} key={el.rank} rank={el.rank} teamName={el.team} />;
});

export default function teamList() {
  return (
    <div className="team_list">
      <div className="team_list_inner">
        <h3 className="brighter">Skeleton League</h3>
        <div className="own_position">
          <h4>Your Position</h4>
          {user}
        </div>
        <div className="table">
          <h4>Leaderboard</h4>
          {userElements}
        </div>
      </div>
    </div>
  );
}
