import React from "react";
import "./Player.css";

export default function player(props) {
  return (
    <div className="player">
      <button onClick={() => props.handleClick(props.role, props.name)}>
        <p>DRAFT</p>
      </button>
      <div className="player_name">
        <div className="img_container">
          <div className="logo__container_sm">
            <img
              src={require(`./orgLogo/${props.team}.png`)}
              alt="org logo"
              className="logo_sm"
            ></img>
          </div>
          <div className="image_sm">
            <img
              src={require(`./${props.role}/${props.img}.png`)}
              alt="player"
              className="player__image_sm"
            ></img>
          </div>
        </div>
        <div id="name_text_container">
          <h4>{props.name}</h4>
          <p>{props.team}</p>
        </div>
      </div>
      <div className="player_stats">
        <div className="stats">
          <p>Value</p>
          <h4>{props.val}</h4>
        </div>
        <div className="stats">
          <p>Points</p>
          <h4>{props.ov}</h4>
        </div>
        <div className="stats">
          <p>KDA</p>
          <h4>{props.kda}</h4>
        </div>
        <div className="stats">
          <p>AVG CS</p>
          <h4>{props.avcs}</h4>
        </div>
        <div className="stats">
          <p>Solo Kills</p>
          <h4>{props.sk}</h4>
        </div>
        <div className="stats">
          <p>First Blood</p>
          <h4>{props.fb}</h4>
        </div>
      </div>
    </div>
  );
}
