import React, { useState, useRef, useEffect } from "react";
import "./Upcoming.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronDown } from "@fortawesome/free-solid-svg-icons";
import C9logo from "./C9.png";
import OTlogo from "./100T.png";
import CLGlogo from "./CLG.png";
import TSMlogo from "./TSM.png";
import GGlogo from "./GG.png";
import TLlogo from "./TL.png";
import FLYlogo from "./FLY.png";
import DIGlogo from "./DIG.png";
import IMTlogo from "./IMT.png";
import EGlogo from "./EG.png";

export default function upcoming() {

  const [day, setDay] = useState(true);

  const ref = useRef(null);

  const changeDays = () => {
    setDay((prevDay) => !prevDay);
  };

  useEffect(() => {
    const handleClick = (event) => {
      let x = event.layerX - 60;
      let y = event.layerY - 55;

      // console.log(x, y);
      // console.log(event.target);

      let ripples = document.createElement("span");
      ripples.classList.add("rippleSpan");
      ripples.style.left = x + "px";
      ripples.style.top = y + "px";
      event.target.parentNode.appendChild(ripples);

      setTimeout(() => {
        ripples.remove();
      }, 700);
    };

    const element = ref.current;

    element.addEventListener("click", handleClick);

    return () => {
      element.removeEventListener("click", handleClick);
    };
  }, [day]);

  return (
    <div className="upcoming" data-scroll>
      <h3 className="brighter">Gameweek 9</h3>
      <div className="btn_container">
        <div className="button_marker">
          <button
            className="upcoming_btn highest"
            ref={ref}
            onClick={() => changeDays()}
          >
            {day ? "Show Sunday" : "Show Saturday"}
          </button>
        </div>
      </div>
      {day ? (
        <div className="schedule">
          <p className="day brighter">Saturday 19th September</p>
          <div className="schedule_element">
            <div className="side">
              <div className="side_bg"></div>
              <img src={TSMlogo} alt="logos" />
            </div>
            <div className="vs">
              <p>vs</p>
            </div>
            <div className="side">
              <div className="side_bg"></div>
              <img src={CLGlogo} alt="logos" />
            </div>
          </div>

          <div className="schedule_element">
            <div className="side">
              <img src={C9logo} alt="logos" />
              <div className="side_bg"></div>
            </div>
            <div className="vs">
              <p>vs</p>
            </div>
            <div className="side">
              <img src={GGlogo} alt="logos" />
              <div className="side_bg"></div>
            </div>
          </div>

          <div className="schedule_element">
            <div className="side">
              <div className="side_bg"></div>
              <img src={OTlogo} alt="logos" />
            </div>
            <div className="vs">
              <p>vs</p>
            </div>
            <div className="side">
              <div className="side_bg"></div>
              <img src={EGlogo} alt="logos" />
            </div>
          </div>

          <div className="schedule_element">
            <div className="side">
              <div className="side_bg"></div>
              <img src={TLlogo} alt="logos" />
            </div>
            <div className="vs">
              <p>vs</p>
            </div>
            <div className="side">
              <div className="side_bg"></div>
              <img src={FLYlogo} alt="logos" />
            </div>
          </div>

          <div className="schedule_element">
            <div className="side">
              <div className="side_bg"></div>
              <img src={IMTlogo} alt="logos" />
            </div>
            <div className="vs">
              <p>vs</p>
            </div>
            <div className="side">
              <div className="side_bg"></div>
              <img src={DIGlogo} alt="logos" />
            </div>
          </div>
        </div>
      ) : (
        <div className="schedule">
          <p className="day">Sunday 20th September</p>

          <div className="schedule_element">
            <div className="side">
              <div className="side_bg"></div>
              <img src={C9logo} alt="logos" />
            </div>
            <div className="vs">
              <p>vs</p>
            </div>
            <div className="side">
              <div className="side_bg"></div>
              <img src={IMTlogo} alt="logos" />
            </div>
          </div>

          <div className="schedule_element">
            <div className="side">
              <div className="side_bg"></div>
              <img src={EGlogo} alt="logos" />
            </div>
            <div className="vs">
              <p>vs</p>
            </div>
            <div className="side">
              <div className="side_bg"></div>
              <img src={FLYlogo} alt="logos" />
            </div>
          </div>

          <div className="schedule_element">
            <div className="side">
              <div className="side_bg"></div>
              <img src={TLlogo} alt="logos" />
            </div>
            <div className="vs">
              <p>vs</p>
            </div>
            <div className="side">
              <div className="side_bg"></div>
              <img src={GGlogo} alt="logos" />
            </div>
          </div>

          <div className="schedule_element">
            <div className="side">
              <div className="side_bg"></div>
              <img src={CLGlogo} alt="logos" />
            </div>
            <div className="vs">
              <p>vs</p>
            </div>
            <div className="side">
              <div className="side_bg"></div>
              <img src={DIGlogo} alt="logos" />
            </div>
          </div>

          <div className="schedule_element">
            <div className="side">
              <div className="side_bg"></div>
              <img src={TSMlogo} alt="logos" />
            </div>
            <div className="vs">
              <p>vs</p>
            </div>
            <div className="side">
              <div className="side_bg"></div>
              <img src={OTlogo} alt="logos" />
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
