import React from "react";
import "./PlayerList.css";

import { ReactComponent as Cross } from "../images/Cross.svg";

import Player from "./Player/Player";

import playerData from "../playerData";

export default function playerList(props) {
  const playerArray = playerData.filter((player) => player.role === props.role && player.selected === "yes");

  const playerElements = playerArray.map((el) => {
    return (
      <Player
        val={el.value}
        name={el.name}
        key={el.name}
        team={el.team}
        img={el.img}
        role={el.role}
        ov={el.ov}
        kda={el.kda}
        avcs={el.avcs}
        sk={el.soloKills}
        fb={el.firstBlood}
        handleClick={props.selectionFunction}
      />
    );
  });

  return (
    <div className="player_list">
      <div className="players_container">
        <button
          className="close_player_info"
          onClick={() => props.closeFunction(0)}
        >
          <Cross className="close_symbol" />
        </button>
        {playerElements}
      </div>
    </div>
  );
}
